.App {
  text-align: center;
  font-family: 'Geneva', sans-serif;
  color: #1a1a1a
}

/* Combining Media Queries */
@media (max-width: 768px) {
  .App-header {
    padding: 10px;
    font-size: calc(8px + 2vmin);
  }
}

.App-header {
  background-color: #ffffff;
  color: #1a1a1a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.App-header h1 {
  font-family: 'Proxima Nova', sans-serif;
  font-size: 3.5rem;
  margin-bottom: 0.5em;
  color: #333333
}

.App-logo {
  height: 50vmin;
  pointer-events: none;
  animation: App-logo-spin infinite 40s lineal;
}


.App-button {
  font-size: 1rem;
  padding: 10px 20px;
  color: #333333;
  border-radius: 5px;
  cursor: pointer;
  transition: blackground-color 0.3s ease, color 0.3 ease;
  display: inline-block;
  font-weight:bold;
  
}

.App-button:hover {
  background-color: #e6e6e6;
  color: #1a1a1a
  
}

.App-link {
  margin: 15px;
  display: inline-block;
  color: #ecf0f4;
  font-size: 1rem;
  text-decoration: underline;
  border-radius: 5px;
  transition: color 0.3s ease, border-color 0.3s ease;
  padding: 10px 20px;
}

.App-link:hover {
  color: #cfaa88;
  text-decoration: none;
  border-color: #cfaa88;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}